
.body {
  margin-top: 25px;
  display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	justify-content: center;
	align-items: center;
}

.Logo {
  width: 150px;
}

h1 {
  font-size: 1rem;
}

.form-box {
  width: 95%;
  padding: 25px 20px;
  font-size: 0.9rem;
  margin-bottom: 20px;
  margin-top: 5px;
  color: #374151;
  box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.1), 0px 4px 6px rgba(0, 0, 0, 0.05);
  display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	justify-content: center;
	align-items: stretch;
}

.slogan {
  font-weight: 400;
}

.italic {
  font-style: italic;
}

.button-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.form-button {
  background-color: #F87171;
  width: 90px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  border: none;
  color: #374151;
  font-size: 0.7rem
}